.sidenav {
  list-style: none;
  font-size: 1.4rem;
  margin-top: 3.5rem; }
  @media only screen and (max-width: 56.25em) {
    .sidenav {
      margin-top: 0;
      display: flex;
      justify-content: space-between; } }
  .sidenav__item {
    position: relative; }
    .sidenav__item:not(:last-child) {
      margin-bottom: .5rem; }
      @media only screen and (max-width: 56.25em) {
        .sidenav__item:not(:last-child) {
          margin-bottom: 0; } }
    @media only screen and (max-width: 56.25em) {
      .sidenav__item {
        flex: 1; } }
  .sidenav__item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--color-primary);
    height: 100%;
    width: 3px;
    transform: scaleY(0);
    transition: transform 0.2s, width cubic-bezier(1, -0.09, 0.07, 1.08) 0.2s; }
  .sidenav__item:hover::before, .sidenav__item--active::before {
    transform: scaleY(1);
    width: 100%; }
  .sidenav__item:active::before {
    background-color: var(--color-primary-light); }
  .sidenav__link:visited, .sidenav__link:link {
    color: var(--color-grey-light-1);
    text-decoration: none;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    padding: 1.5rem 3rem;
    position: relative;
    z-index: 10; }
    @media only screen and (max-width: 56.25em) {
      .sidenav__link:visited, .sidenav__link:link {
        justify-content: center; } }
    @media only screen and (max-width: 37.5em) {
      .sidenav__link:visited, .sidenav__link:link {
        flex-direction: column;
        padding: 1rem 1rem; } }
  .sidenav__icon {
    height: 1.75rem;
    width: 1.75rem;
    fill: currentColor;
    margin-right: 2rem; }
    @media only screen and (max-width: 37.5em) {
      .sidenav__icon {
        margin-right: 0;
        margin-bottom: .5rem; } }

.legal {
  font-size: 1.2rem;
  color: var(--color-grey-light-4);
  text-align: center;
  padding: 2.5rem; }
  @media only screen and (max-width: 56.25em) {
    .legal {
      display: none; } }

.gallery {
  display: flex; }
  .gallery__photo {
    width: 100%;
    display: block; }

.overview {
  border-bottom: var(--line);
  display: flex;
  align-items: center; }
  .overview__heading {
    font-size: 2.25rem;
    font-weight: 300;
    padding: 1.5rem 3rem;
    text-transform: uppercase;
    letter-spacing: 1px; }
    @media only screen and (max-width: 37.5em) {
