.btn__inline {
  border: none;
  font-size: inherit;
  color: var(--color-primary);
  background-color: transparent;
  padding-bottom: .2rem;
  border-bottom: 1px solid currentColor;
  display: inline-block;
  cursor: pointer;
  transition: all .2s; }
  .btn__inline span {
    margin-left: 3px;
    transition: margin-left .2s; }
  .btn__inline:hover {
    color: var(--color-grey-dark-1);
    outline: none;
    animation: pulsate 1s infinite; }
    .btn__inline:hover span {
      margin-left: 8px; }
  .btn__inline:focus {
    outline: none; }

@keyframes pulsate {
  0% {
    transform: scale(1);
    box-shadow: none; }
  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25); }
  100% {
    transform: scale(1);
    box-shadow: none; } }

.paragraph:not(:last-of-type) {
  margin-bottom: 2rem; }

.list {
  list-style: none;
  margin: 3rem 0;
  padding: 3rem 0;
  border-top: var(--line);
  border-bottom: var(--line);
  display: flex;
  flex-wrap: wrap; }
  .list__item {
    flex: 0 0 50%;
    margin-bottom: .7rem; }
  .list__item::before {
    content: '';
    display: inline-block;
    height: 1rem;
    width: 1rem;
    margin-right: .7rem;
    background-color: var(--color-primary);
    mask-image: url("../img/chevron-thin-right.svg");
    mask-size: cover;
    -webkit-mask-image: url("../img/chevron-thin-right.svg");
    -webkit-mask-size: cover; }

.recommend {
  font-size: 1.3rem;
  color: var(--color-grey-dark-3);
  display: flex;
  align-items: center; }

.recommend__count {
  margin-right: auto; }

.recommend__friends {
  display: flex; }

.recommend__photo {
  box-sizing: content-box;
  display: inline-block;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  border: 3px solid #fff;
  margin-right: -2rem; }

.review {
  background-color: #fff;
  padding: 3rem;
  margin-bottom: 3.5rem;
  box-shadow: var(--shadow-light);
  position: relative;
  overflow: hidden; }
  @media only screen and (max-width: 56.25em) {
    .review {
      padding: 2rem;
      margin-bottom: 2.5rem; } }
  .review__text {
    margin-bottom: 2rem;
    position: relative;
    z-index: 10; }
  .review__user {
    display: flex;
    align-items: center; }
  .review__user-photo {
    height: 4.5rem;
    width: 4.5rem;
    border-radius: 50%; }
  .review__user-box {
    margin-left: 1.5rem;
    margin-right: auto; }
  .review__user-name {
    font-size: 1.1rem;
    font-weight: 600;
    text-transform: uppercase;
