      padding: 2rem 0; } }
  .cta__text {
    font-size: 2rem;
    font-weight: 300;
    text-transform: uppercase;
    margin-bottom: 2.5rem; }
    @media only screen and (max-width: 56.25em) {
      .cta__text {
        margin-bottom: 1.5rem; } }
    @media only screen and (max-width: 37.5em) {
      .cta__text {
        font-size: 1.5rem; } }

.btn {
  border: none;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  border-radius: 100px;
  font-size: 1.5rem;
  font-weight: 300;
  text-transform: uppercase;
  color: #fff;
  position: relative;
  overflow: hidden; }
  @media only screen and (max-width: 37.5em) {
    .btn {
      font-size: 1.2rem; } }
  .btn > * {
    display: inline-block;
    height: 100%;
    width: 100%;
    transition: all .5s; }
  .btn__invisible {
    position: absolute;
    padding: 2rem 0;
    top: -100%;
    left: 0; }
  .btn__visible {
    padding: 2rem 7rem; }
  .btn:hover .btn__invisible, .btn:hover .btn__visible {
    transform: translateY(100%); }
  .btn:hover {
    background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
    animation: pulsate 1s infinite; }
  .btn:focus {
    outline: none; }
